* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen-Sans, Ubuntu, Cantarell,
    'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

img {
  display: block;
  max-width: 100%;
}

#datetime-picker {
  border: 2px solid teal;
  border-radius: 5px;
  margin-bottom: 10px;
}
.timer {
  display: flex;
  flex-direction: row;
  text-align: center;
}
.field {
  border: 2px solid teal;
  margin-bottom: 10px;
  border-radius: 5px;
  width: 170px;
}
.field:not(:first-child) {
  margin-left: 10px;
}

.start-btn {
  width: 50px;
  height: 22px;
  border-radius: 5px;
  border: 2px solid teal;
}
